.color-picker {
  display: flex;
  align-items: center;
  align-content: stretch;
  justify-content: space-evenly;
}

.color-button-active {
  margin: 1vw;
  height: var(--button-size-active);
  border-radius: 3px;
  border: 2px solid #252A34;
  aspect-ratio: 1;
  animation-duration: 0.25s;
  animation-name: foo;
}

@keyframes foo {
  from {
    height: var(--button-size-inactive);
  }
  to {
    height: var(--button-size-active);
  }
}

.color-button-inactive {
  margin: 1vw;
  height: var(--button-size-inactive);
  border-radius: 3px;
  border: 2px solid #252A34;
  aspect-ratio: 1;
  animation-duration: 0.25s;
  animation-name: bar;
}

@keyframes bar {
  from {
    height: var(--button-size-active);
  }
  to {
    height: var(--button-size-inactive);
  }
}

.square {
  border: 0.5px solid #252A34;
  padding: 0;
  flex: 1;
}

.hint-row {
  border: 0.5px solid #252A34;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  padding: 0;
  text-align: center;
  flex: 1;
}

.hint-column {
  align-items: center;
  border: 0.5px solid #252A34;
  display: flex;
  justify-content: space-evenly;
  padding: 0;
  text-align: center;
  flex: 1;
}

.hints-row {
  border: 0.5px solid #252A34;
  display: flex;
  margin-bottom: -2px;
  grid-column: 2;
  grid-row: 1;
}

.hints-column {
  border: 0.5px solid #252A34;
  display: flex;
  flex-direction: column;
  margin-right: -2px;
  grid-column: 1;
  grid-row: 2;
}

.grid-segment-row {
  display: flex;
  flex: 1;
}

.grid-segment {
  border: 1px solid #252A34;
  display: flex;
  flex-direction: column;
  flex: 1;
}

.grid-row {
  display: flex;
  flex: 1;
}

.grid {
  border: 1px solid #252A34;
  display: flex;
  flex-direction: column;
  grid-column: 2;
  grid-row: 2;
}

.board {
  display: grid;
  grid-template-columns: 1fr 4fr;
  grid-template-rows: 1fr 4fr;
  aspect-ratio: 1;
  flex: 4;
}

.undo {
  background-image: url("https://cdn4.iconfinder.com/data/icons/ionicons/512/icon-ios7-undo-512.png");
  background-size: 100%;
  border-radius: 3px;
  border: 2px solid #252A34;
  margin: 1vw;
  height: var(--button-size-inactive);
  aspect-ratio: 1;
}

.buttons {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.game {
  padding: 20px;
  align-items: center;
  display: flex;
  flex-direction: column;
  height: 100%;
}

@media (orientation: landscape) {
  #root {
    height: 100%;
    aspect-ratio: 1;
  }
}

@media (orientation: portrait) {
  #root {
    width: 100%;
    aspect-ratio: 1;
  }
}

#root {
  margin: 0;
  --button-size-active: 8vh;
  --button-size-inactive: 6vh;
}

body {
  display: flex;
  justify-content: center;
  margin: 0;
}

html {
  font-size: 3vmin;
  background-color: #EAEAEA;
  color: #252A34;
}
